.App{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;

}
.editor{
  width: 30%;
  min-width: 300px;
  height: 40%;
  margin-top: 15px;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}
#editor{
  background-color: #dcdde1;
}
.editorTitle{
  width: 100%;
  height: 10%;
  background-color: #718093;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 5px;
}
i{
  margin-left: 5px;
}
p{
  margin: 0;
  font-family: Lato, sans-serif;
  font-weight: bold;
}
textarea{
  width: 100%;
  height: 90%;
}
.previstaContainer{
  margin-top: 15px;
  display: flex;
  flex-direction: column;
  width: 50%;
  height: 60%;
}
.prevista{
  width: 100%;
  min-width: 500px;
  height: 100%;
  background-color:#dcdde1 ;
  margin-top: 0;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
  margin-bottom: 15px;
  overflow-y: scroll;

}
.prevista img{
  max-width: 50%;
  max-height: fit-content;

}